





































































































































.insurance-select {
    width: 280px;
    height: 50px;
    background: #FFFFFF;
    box-shadow: 0 2px 4px 0 rgba(204, 204, 204, 0.5);
    border-radius: 4px;
    position: relative;

    &:hover {
        .insurance-select__list {
            .list-wrapper {
                padding: 10px;

                &__container {
                    max-height: 150px;
                    overflow-y: auto;
                }
            }
        }
    }

    &__icon {
        width: 16px;
        height: 16px;
        position: absolute;
        top: 17px;
        left: 8px;
    }

    &__icon__right {
        width: 20px;
        height: 20px;
        position: absolute;
        top: 15px;
        right: 8px;
    }

    &__list {
        padding: 10px 0;

        .list {
            height: 40px;
            border-radius: 2px;
            padding: 0 8px;
            cursor: pointer;
            display: flex;
            justify-content: space-between;
            align-items: center;
            transition: background .3s;

            &.selected {
                background: rgba(81, 176, 139, 0.14);
            }

            &:hover {
                background: rgba(81, 176, 139, 0.14);
            }

            &-wrapper {
                background: #FFFFFF;
                box-shadow: 0 2px 4px 0 rgba(204, 204, 204, 0.5);
                border-radius: 4px;
                position: fixed;
                width: 260px;
                z-index: 10;
                padding: 0 10px;
                transition: padding .3s;

                &__container {
                    max-height: 0;
                    overflow-y: auto;
                    transition: max-height .3s;
                }
            }

            &-text {
                font-size: 16px;
                font-family: MicrosoftYaHei;
                color: #383838;
                line-height: 40px;
            }

            img {
                width: 16px;
                height: 16px;
            }
        }
    }

    input {
        -webkit-appearance: none;
        background-color: #FFF;
        background-image: none;
        -webkit-border-radius: 4px;
        border-radius: 4px;
        border: 0 solid #DCDFE6;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        color: #606266;
        display: inline-block;
        font-size: inherit;
        height: 50px;
        line-height: 50px;
        outline: 0;
        padding: 0 15px;
        -webkit-transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
        transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
        width: 100%;
        cursor: pointer;

        &.selected {
            padding-left: 32px;
        }

        &::placeholder {
            font-size: 16px;
            font-family: MicrosoftYaHei;
            color: #AFAFAF;
            line-height: 16px;
        }
    }
}
